import React, { useState } from "react";
import {
  Box,
  Heading,
  Text,
  FormControl,
  FormLabel,
  Input,
  Textarea,
  Button,
  VStack,
  useToast,
} from "@chakra-ui/react";
import { addDoc, collection, serverTimestamp } from "firebase/firestore";
// firebaseConfig.js
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore";

const firebaseConfig = {
  apiKey: "AIzaSyAkHCm9B4KxAt3n3R6rsdUvMqiBoHE3MVE",
  authDomain: "calucid-dashboard.firebaseapp.com",
  projectId: "calucid-dashboard",
  storageBucket: "calucid-dashboard.firebasestorage.app",
  messagingSenderId: "198950789396",
  appId: "1:198950789396:web:c2ccc897a78f6dd4f6e201",
  measurementId: "G-G2NRCTBH0F"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

// Initialize Firestore
const db = getFirestore(app);

const ContactPage = () => {
  const toast = useToast();
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    subject: "",
    message: "",
  });
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [id]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      await addDoc(collection(db, "communications"), {
        ...formData,
        createdAt: serverTimestamp(), // Add timestamp
        website: "moneyoptimalmanagement-com",
        type: "contactpage",
      });
      toast({
        title: "Form Submitted",
        description: "We have received your message.",
        status: "success",
        duration: 5000,
        isClosable: true,
      });
      setFormData({
        name: "",
        email: "",
        subject: "",
        message: "",
      });
    } catch (error) {
      console.error("Error adding document: ", error);
      toast({
        title: "Submission Failed",
        description: "There was an error submitting your message. Please try again.",
        status: "error",
        duration: 5000,
        isClosable: true,
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box bg="gray.50" minH="100vh" py={10} px={5}>
      <Box bg="white" maxW="600px" mx="auto" p={8} rounded="md" shadow="lg">
        <Heading as="h1" size="xl" mb={4} textAlign="center">
          Contact Us
        </Heading>
        <Text mb={6} textAlign="center">
          We'd love to hear from you! Please fill out the form below, and we'll get back to you as soon as possible.
        </Text>
        <form onSubmit={handleSubmit}>
          <VStack spacing={4}>
            <FormControl id="name" isRequired>
              <FormLabel>Your Name</FormLabel>
              <Input
                placeholder="John Doe"
                value={formData.name}
                onChange={handleChange}
              />
            </FormControl>
            <FormControl id="email" isRequired>
              <FormLabel>Email Address</FormLabel>
              <Input
                type="email"
                placeholder="you@example.com"
                value={formData.email}
                onChange={handleChange}
              />
            </FormControl>
            <FormControl id="subject">
              <FormLabel>Subject</FormLabel>
              <Input
                placeholder="What is this about?"
                value={formData.subject}
                onChange={handleChange}
              />
            </FormControl>
            <FormControl id="message" isRequired>
              <FormLabel>Message</FormLabel>
              <Textarea
                placeholder="Your message here..."
                rows={6}
                value={formData.message}
                onChange={handleChange}
              />
            </FormControl>
            <Button
              type="submit"
              colorScheme="green"
              width="full"
              size="lg"
              isLoading={loading}
            >
              Submit
            </Button>
          </VStack>
        </form>
      </Box>
    </Box>
  );
};

export default ContactPage;
