import React, { useState } from "react";
import {
  Box,
  Heading,
  Text,
  FormControl,
  FormLabel,
  Select,
  Input,
  Button,
  VStack,
  useToast,
} from "@chakra-ui/react";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore";
import { addDoc, collection, serverTimestamp, Timestamp } from "firebase/firestore";
const firebaseConfig = {
  apiKey: "AIzaSyAkHCm9B4KxAt3n3R6rsdUvMqiBoHE3MVE",
  authDomain: "calucid-dashboard.firebaseapp.com",
  projectId: "calucid-dashboard",
  storageBucket: "calucid-dashboard.firebasestorage.app",
  messagingSenderId: "198950789396",
  appId: "1:198950789396:web:c2ccc897a78f6dd4f6e201",
  measurementId: "G-G2NRCTBH0F"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

// Initialize Firestore
const db = getFirestore(app);

const Appointment = () => {
  const toast = useToast();

  const calculateAvailableDates = () => {
    const dates = [];
    const today = new Date();
    const todayDate = today.toISOString().split("T")[0];

    for (let i = 0; i < 30; i++) {
      const date = new Date(today);
      date.setDate(today.getDate() + i);
      const formattedDate = date.toISOString().split("T")[0];
      const day = date.getDay();

      if (formattedDate > todayDate && [2, 4, 6, 0].includes(day)) {
        dates.push(formattedDate);
      }
    }

    return dates;
  };

  const availableDates = calculateAvailableDates();
  const availableTimes = ["3:00 PM CST", "4:00 PM CST", "5:00 PM CST", "6:00 PM CST", "7:00 PM CST"];

  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
    date: "",
    time: "",
    notes: "",
  });
  const [availableSlots, setAvailableSlots] = useState([]);

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [id]: value,
    }));

    if (id === "date") {
      const date = value;
      const day = new Date(date).getDay();
      if ([2, 4, 6, 0].includes(day)) {
        setAvailableSlots(availableTimes);
      } else {
        setAvailableSlots([]);
      }
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      // Combine date and time into a JavaScript Date object
      const date = new Date(formData.date);
      const [hours, minutes] = formData.time.split(/[: ]/); // Split time into hours and minutes
      const isPM = formData.time.includes("PM");

      // Set hours and minutes correctly based on AM/PM
      date.setHours(isPM ? parseInt(hours) + 12 : parseInt(hours));
      date.setMinutes(parseInt(minutes));

      // Convert to Firestore Timestamp
      const appointmenttime = Timestamp.fromDate(date);

      // Add data to Firestore
      await addDoc(collection(db, "appointments"), {
        ...formData,
        appointmenttime, // Firestore Timestamp object
        timestamp: serverTimestamp(), // Created at timestamp
        website:"moneyoptimalmanagement-com"
      });

      toast({
        title: "Appointment Booked",
        description: "Your appointment has been successfully scheduled.",
        status: "success",
        duration: 5000,
        isClosable: true,
      });

      // Reset form
      setFormData({
        name: "",
        email: "",
        phone: "",
        date: "",
        time: "",
        notes: "",
      });
      setAvailableSlots([]);
    } catch (error) {
      console.error("Error booking appointment: ", error);
      toast({
        title: "Submission Failed",
        description: "There was an error scheduling your appointment. Please try again.",
        status: "error",
        duration: 5000,
        isClosable: true,
      });
    }
  };

  return (
    <Box bg="gray.50" minH="100vh" py={10} px={5}>
      <Box bg="white" maxW="600px" mx="auto" p={8} rounded="md" shadow="lg">
        <Heading as="h1" size="xl" mb={4} textAlign="center">
          Book an Appointment
        </Heading>
        <Text mb={6} textAlign="center">
          Please fill out the form below to schedule your appointment. We look forward to assisting you.
        </Text>
        <form onSubmit={handleSubmit}>
          <VStack spacing={4}>
            <FormControl id="name" isRequired>
              <FormLabel>Your Name</FormLabel>
              <Input
                placeholder="John Doe"
                value={formData.name}
                onChange={handleChange}
              />
            </FormControl>
            <FormControl id="email" isRequired>
              <FormLabel>Email Address</FormLabel>
              <Input
                type="email"
                placeholder="you@example.com"
                value={formData.email}
                onChange={handleChange}
              />
            </FormControl>
            <FormControl id="phone" isRequired>
              <FormLabel>Phone Number</FormLabel>
              <Input
                type="tel"
                placeholder="(123) 456-7890"
                value={formData.phone}
                onChange={handleChange}
              />
            </FormControl>
            <FormControl id="date" isRequired>
              <FormLabel>Preferred Date</FormLabel>
              <Select
                placeholder="Select a date"
                value={formData.date}
                onChange={handleChange}
              >
                {availableDates.map((date) => (
                  <option key={date} value={date}>
                    {new Date(date).toDateString()}
                  </option>
                ))}
              </Select>
            </FormControl>
            <FormControl id="time" isRequired>
              <FormLabel>Preferred Time</FormLabel>
              <Select
                placeholder="Select a time"
                value={formData.time}
                onChange={handleChange}
                isDisabled={!availableSlots.length}
              >
                {availableSlots.length > 0 ? (
                  availableSlots.map((time) => (
                    <option key={time} value={time}>
                      {time}
                    </option>
                  ))
                ) : (
                  <option value="" disabled>
                    No available times for the selected date
                  </option>
                )}
              </Select>
            </FormControl>

            <FormControl id="notes">
              <FormLabel>Additional Notes</FormLabel>
              <Input
                placeholder="Any specific details or requests"
                value={formData.notes}
                onChange={handleChange}
              />
            </FormControl>
            <Button type="submit" colorScheme="blue" width="full" size="lg">
              Schedule Appointment
            </Button>
          </VStack>
        </form>
      </Box>
    </Box>
  );
};

export default Appointment;