import React from 'react';
import { Box, Container, Text, VStack, Heading, Button, HStack, Link, Divider, Image, Flex } from '@chakra-ui/react';
import { EmailIcon, PhoneIcon, LocationIcon, InfoIcon } from '@chakra-ui/icons';

function About() {
  return (
    <div>
      {/* About Section */}
      <Box bg="blue.900" color="white" minHeight="100vh">
        <Container maxW="container.md" py={16}>
          <VStack spacing={12} align="start">

            {/* Page Title */}
            <Heading as="h1" fontSize="4xl" color="green.400" textAlign="center">
              About Money Optimal Management Financial Services
            </Heading>

            {/* Introduction */}
            <Text fontSize="xl">
              Welcome to Money Optimal Management Financial Services, a trusted local financial services provider committed
              to helping families and individuals achieve financial security. Based in Germantown, Tennessee, we specialize in
              providing tailored solutions for life insurance, annuities, and debt settlement services. Our mission is to offer
              personalized guidance and trusted financial products that give our clients peace of mind for the future.
            </Text>
            <Text fontSize="xl" mt={4}>
              With years of experience in the financial services industry, we understand that each person has unique needs and
              goals. Thats why we take the time to get to know you, ensuring that our solutions fit your specific circumstances.
              Whether you're looking for life insurance to protect your loved ones, planning for retirement with annuities, or
              struggling with debt and in need of a settlement plan, we are here to help.
            </Text>

            <Text fontSize="xl" mt={4}>
            Money Optimal Management Financial Services can help you achieve peace of mind and optimal financial security through our comprehensive life insurance and debt settlement services.            </Text>


            <Flex
              justifyContent="center"
              alignItems="center"
            >
              <Image
                src={`${process.env.PUBLIC_URL}/images/money-2696228_1280.jpg`}
                alt="Finance Image"
                maxW="90vw" // Ensures the image won't be larger than 80% of the viewport width
                w="100%" // Make sure it takes the full width of its container
                h="auto" // Let the height adjust automatically based on the width
                objectFit="cover" // Maintain aspect ratio, cropping if necessary
              />
            </Flex>
            
            {/* Our Approach */}
            <Box>
              <Heading as="h2" fontSize="3xl" color="green.400">
                Our Approach to Financial Services
              </Heading>
              <Text fontSize="lg" mt={4}>
                At Money Optimal Management Financial Services, we pride ourselves on our personalized approach. We understand
                that each individual's financial situation is unique, so we dont believe in one-size-fits-all solutions. 
                Instead, we take the time to understand your specific goals, challenges, and needs, and then work with you to 
                create a customized financial strategy that fits your life.
              </Text>
              <Text fontSize="lg" mt={4}>
                We focus on building long-lasting relationships with our clients, guiding them through their financial journey
                every step of the way. Whether you need advice on insurance policies, retirement savings, or managing debt, we
                are here to provide expert guidance and support. Our goal is to make sure you feel confident and secure about
                your financial future.
              </Text>
              
            </Box>

            <Divider borderColor="green.400" my={8} />

            {/* Our Services */}
            <Box>
              <Heading as="h2" fontSize="3xl" color="green.400">
                Our Services
              </Heading>
              <Text fontSize="lg" mt={4}>
                We offer a wide range of services designed to meet the diverse needs of our clients. Our primary services
                include:
              </Text>
              <VStack spacing={4} align="start" mt={4}>
                <Text fontSize="lg">
                  - <strong>Life Insurance:</strong> Protecting your familys financial future with comprehensive life
                  insurance policies.
                </Text>
                <Text fontSize="lg">
                  - <strong>Annuities:</strong> Providing a reliable source of income for your retirement years.
                </Text>
                <Text fontSize="lg">
                  - <strong>Debt Settlement:</strong> Helping clients eliminate or reduce their debt through debt
                  settlement programs and expert negotiation.
                </Text>
              </VStack>
            </Box>

            <Divider borderColor="green.400" my={8} />

            {/* Local Community Focus */}
            <Box>
              <Heading as="h2" fontSize="3xl" color="green.400">
                Serving the Germantown, TN Community
              </Heading>
              <Text fontSize="lg" mt={4}>
                Money Optimal Management Financial Services is proud to be a part of the Germantown, TN community. We are
                committed to serving local families and businesses with financial products and services that provide real,
                lasting value. Our office is conveniently located at:
              </Text>
              <br></br>
              <Flex
              justifyContent="center"
              alignItems="center"
            >
              <Image
                src={`${process.env.PUBLIC_URL}/images/Germantown.jpg`}
                alt="Town Image"
                maxW="90vw" // Ensures the image won't be larger than 80% of the viewport width
                w="100%" // Make sure it takes the full width of its container
                h="auto" // Let the height adjust automatically based on the width
                objectFit="cover" // Maintain aspect ratio, cropping if necessary
              />
            </Flex>
              <HStack spacing={4} mt={4}>
                <InfoIcon boxSize={6} color="blue.200" />
                <Text fontSize="xl">
                  9155 Crestwyn Hills Dr. Ste. 200, Germantown, TN, United States, Tennessee
                </Text>
              </HStack>
              <Text fontSize="lg" mt={4}>
                Being locally based allows us to understand the unique challenges and opportunities our community faces. We
                believe in fostering strong relationships with our clients, and we take pride in knowing that our services are
                helping families and individuals right here in Germantown and beyond.
              </Text>
            </Box>

            <Divider borderColor="green.400" my={8} />

            {/* Social Media Links */}
            <Box>
              <Heading as="h3" fontSize="2xl" color="green.400">
                Connect with Us on Social Media
              </Heading>
              <Text fontSize="lg" mt={4}>
                Stay up to date with the latest financial tips, news, and updates from Money Optimal Management Financial
                Services. Follow us on social media to learn more about our services and get advice from our team.
              </Text>
              <HStack spacing={6} mt={4}>
                <Link href="https://www.instagram.com/Latasha.Nichols" isExternal color="green.200">
                  Instagram
                </Link>
                <Link href="https://www.facebook.com/share/RE4tCYLRBiSoxZ33/?mibextid=LQQJ4d" isExternal color="green.200">
                  Facebook
                </Link>
              </HStack>
            </Box>

            <Divider borderColor="green.400" my={8} />

            {/* Contact Information */}
            <Box>
              <Heading as="h3" fontSize="2xl" color="green.400">
                Get in Touch with Us
              </Heading>
              <Text fontSize="lg" mt={4}>
                We would love to hear from you! If you have any questions about our services or would like to speak with one
                of our financial experts, dont hesitate to reach out. You can contact us through the following methods:
              </Text>
              <HStack spacing={6} mt={4}>
                <PhoneIcon boxSize={6} color="blue.200" />
                <Text fontSize="xl">+1 901-550-4000</Text>
              </HStack>
              <HStack spacing={6} mt={4}>
                <EmailIcon boxSize={6} color="blue.200" />
                <Text fontSize="xl">
                  <Link href="mailto:latashanicholsphp@gmail.com" color="blue.200">
                    latashanicholsphp@gmail.com
                  </Link>
                </Text>
              </HStack>
              <Button colorScheme="green" size="lg" mt={8} as="a" href="/contact">
                Contact Us
              </Button>
            </Box>

          </VStack>
        </Container>
      </Box>
    </div>
  );
}

export default About;
