import React from 'react';
import { Box, Container, Text, VStack, Heading, Button, Divider, Flex, Image } from '@chakra-ui/react';
import { ArrowForwardIcon, EmailIcon, PhoneIcon } from '@chakra-ui/icons';

function Services() {
  return (
    <div>
      {/* Services Section */}
      <Box bg="blue.900" color="white" minHeight="100vh">
        <Container maxW="container.md" py={16}>
          <VStack spacing={12} align="start">
            {/* Page Title */}
            <Heading as="h1" fontSize="5xl" color="green.400" textAlign="center">
              Our Services
            </Heading>

            <Flex
              justifyContent="center"
              alignItems="center"
            >
              <Image
                src={`${process.env.PUBLIC_URL}/images/financial-2860753_1280.jpg`}
                alt="Finance Image"
                maxW="90vw" // Ensures the image won't be larger than 80% of the viewport width
                w="100%" // Make sure it takes the full width of its container
                h="auto" // Let the height adjust automatically based on the width
                objectFit="cover" // Maintain aspect ratio, cropping if necessary
              />
            </Flex>

            {/* Life Insurance Service */}
            <Box>
              <Heading as="h2" fontSize="3xl" color="green.400">
                Life Insurance
              </Heading>
              <Text fontSize="lg" mt={4}>
                Life insurance is an essential financial tool that provides financial security for your loved ones
                after youre gone. It offers peace of mind knowing that your family will be taken care of in case of
                unforeseen events. At Money Optimal Management Financial Services, we offer a range of life insurance
                options to fit your needs and financial goals.
              </Text>
              <Text fontSize="lg" mt={4}>
                There are various types of life insurance policies:
              </Text>
              <VStack spacing={4} align="start" mt={4}>
                <Text fontSize="lg">
                  - <strong>Term Life Insurance:</strong> This is a temporary policy that provides coverage for a set period
                  (e.g., 10, 20, or 30 years). It is typically the most affordable type of life insurance.
                </Text>
                <Text fontSize="lg">
                  - <strong>Whole Life Insurance:</strong> A permanent policy that offers lifetime coverage and can accumulate
                  cash value over time. It is more expensive but can provide long-term benefits.
                </Text>
                <Text fontSize="lg">
                  - <strong>Universal Life Insurance:</strong> This flexible policy combines life coverage with an investment
                  component. You can adjust the death benefit and premium amounts to suit your changing needs.
                </Text>
              </VStack>
              <Text fontSize="lg" mt={4}>
                Life insurance helps replace lost income, pay off debts, and cover final expenses like funeral costs. It also
                provides financial stability for your family during challenging times.
              </Text>
            </Box>

            <Button
              as="a"
              href="/appointment" // Replace with your partner's application URL
              target="_blank"
              colorScheme="green"
              size="lg"
              mt={8}
              rightIcon={<ArrowForwardIcon />}
              _hover={{ bg: "green.500" }}
              >
              Schedule an Appointment
              </Button>

            <Divider borderColor="green.400" my={8} />

            {/* Annuities Service */}
            <Box>
              <Heading as="h2" fontSize="3xl" color="green.400">
                Annuities
              </Heading>
              <Text fontSize="lg" mt={4}>
                Annuities are financial products that provide a steady stream of income, typically for retirement. They can
                be a valuable option for individuals looking for guaranteed income in their later years, making them an
                excellent choice for securing financial stability.
              </Text>
              <Text fontSize="lg" mt={4}>
                There are several types of annuities, each designed to meet different financial needs:
              </Text>
              <VStack spacing={4} align="start" mt={4}>
                <Text fontSize="lg">
                  - <strong>Fixed Annuities:</strong> These provide a guaranteed, fixed income for a set period or for the
                  remainder of your life. Fixed annuities are ideal for individuals seeking stability and predictable
                  income.
                </Text>
                <Text fontSize="lg">
                - <strong>Fixed Index Annuities:</strong> These are tied to a market index, offering growth potential while protecting your principal from market downturns. Fixed index annuities provide a balance of stability and the chance for positive returns without the risks of direct stock market exposure.
                </Text>
                <Text fontSize="lg">
                  - <strong>Immediate Annuities:</strong> This type starts paying income almost immediately after a lump sum
                  investment. Its best suited for those who want to start receiving income right away.
                </Text>
                <Text fontSize="lg">
                  - <strong>Deferred Annuities:</strong> These allow you to invest a sum of money and delay receiving
                  payments until a future date, often during retirement. The goal is to accumulate wealth over time before
                  receiving payouts.
                </Text>
              </VStack>
              <Text fontSize="lg" mt={4}>
                Annuities are an effective tool for managing retirement income, ensuring you won't outlive your savings. They
                can be used alongside other retirement planning strategies to create a comprehensive financial plan.
              </Text>
            </Box>
            <Button
              as="a"
              href="/appointment" // Replace with your partner's application URL
              target="_blank"
              colorScheme="green"
              size="lg"
              mt={8}
              rightIcon={<ArrowForwardIcon />}
              _hover={{ bg: "green.500" }}
              >
              Schedule an Appointment
              </Button>

            <Divider borderColor="green.400" my={8} />

            {/* Debt Settlement Service */}
            <Box>
              <Heading as="h2" fontSize="3xl" color="green.400">
                Debt Settlement
              </Heading>
              <Text fontSize="lg" mt={4}>
                Debt settlement is a process through which you can negotiate with creditors to pay off your debts for less
                than what you owe. At Money Optimal Management Financial Services, we partner with reputable debt settlement
                companies to help clients resolve their financial obligations while avoiding bankruptcy.
              </Text>
              <Text fontSize="lg" mt={4}>
                Debt settlement is an ideal solution for those who are struggling with unsecured debts, such as credit card
                balances, personal loans, and medical bills. Instead of making minimum monthly payments and accruing high
                interest, we can work with creditors to reduce the overall debt amount, making it more manageable.
              </Text>
              <Text fontSize="lg" mt={4}>
                The debt settlement process typically includes the following steps:
              </Text>
              <VStack spacing={4} align="start" mt={4}>
                <Text fontSize="lg">
                  - <strong>Assessment:</strong> We evaluate your financial situation, including the total debt, income, and
                  expenses. This helps determine if debt settlement is the right option for you.
                </Text>
                <Text fontSize="lg">
                  - <strong>Negotiation:</strong> Our team negotiates with creditors to reduce the total amount owed. Often,
                  creditors are willing to settle for less than the full balance to avoid costly collection processes.
                </Text>
                <Text fontSize="lg">
                  - <strong>Settlement:</strong> Once an agreement is reached, well work with you to pay off the reduced debt
                  amount, typically in a lump sum or through manageable installments.
                </Text>
                <Text fontSize="lg">
                  - <strong>Debt Freedom:</strong> After the debt is settled, you can focus on rebuilding your credit and
                  moving forward financially, without the burden of overwhelming debt.
                </Text>
              </VStack>
              <Text fontSize="lg" mt={4}>
                Debt settlement offers a way out for individuals who are overwhelmed by debt. It can provide relief and a
                path to financial freedom, especially when other options like bankruptcy seem too drastic.
              </Text>
              <Button
            colorScheme="green"
            size="lg"
            mt={8}
            rightIcon={<ArrowForwardIcon />}
            as="a"
            href="https://www.sm.phpdebtsolutions.com/links/1082651/656" // Replace with your partner's application URL
            target="_blank"
            _hover={{ bg: "green.500" }}
          >
            Apply for Debt Settlement Now
          </Button>
            </Box>

            <Divider borderColor="green.400" my={8} />

            {/* Contact Information Section */}
            <Box>
              <Heading as="h3" fontSize="2xl" color="green.400">
                Get in Touch with Us
              </Heading>
              <Text fontSize="lg" mt={4}>
                If you have questions about any of our services or would like to speak with a financial expert, we are here to
                help.
              </Text>
              <br></br>
              <Button colorScheme="green" size="lg" as="a" href="mailto:latashanicholsphp@gmail.com">
                Contact Us
              </Button>
            </Box>
          </VStack>
        </Container>
      </Box>
    </div>
  );
}

export default Services;
